import React, { useState } from 'react'
import styled from 'styled-components'
import Web3 from 'web3'
import { Link } from 'react-router-dom';
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Button, Input, Modal, Text } from '@pancakeswap-libs/uikit'
import { NFT, NftFarm } from 'config/constants/newnfts'
import { Nft } from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import { useNFTFarmV2Contract } from 'hooks/useContract'

interface TransferNftModalProps {
  nft: Nft
  tokenIds: number[]
  onSuccess: () => any
  onDismiss?: () => void
}

const Value = styled(Text)`
  font-weight: 600;
`

const ModalContent = styled.div`
  margin-bottom: 16px;
`

const Actions = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
`

const Label = styled.label`
  color: ${({ theme }) => theme.colors.text};
  display: block;
  margin-bottom: 8px;
  margin-top: 24px;
`
// tokenIds is retrieved dynamically from contracts.
const BuyNftModal: React.FC<TransferNftModalProps> = ({ nft, tokenIds, onSuccess, onDismiss }) => {
  const [isLoading, setIsLoading] = useState(false)
  const [values, setValues] = useState({
    address: '',
    tokenId: tokenIds[0],
    tradeId: nft.tradeId,
  })
  const [error, setError] = useState(null)
  const TranslateString = useI18n()
  const { account } = useWallet()

  const NFTFarmV2Contract = useNFTFarmV2Contract(NftFarm)

  const handleConfirm = async () => {
    try {
      const isValidAddress = Web3.utils.isAddress(values.address)

      if (!isValidAddress) {
        setError(TranslateString(999, 'Please enter a valid wallet address'))
      } else {
        const tradeId = nft.tradeId
        await NFTFarmV2Contract.methods
          .transfer(tradeId, values.address)
          .send({ from: account })
          .on('sending', () => {
            setIsLoading(true)
          })
          .on('receipt', () => {
            onDismiss()
            onSuccess()
          })
          .on('error', () => {
            console.error(error)
            setError('Unable to transfer NFT')
            setIsLoading(false)
          })
      }
    } catch (err) {
      console.error('Unable to transfer NFT:', err)
    }
  }

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const { value: inputValue, name } = evt.target
    setValues({ ...values, [name]: inputValue })
  }

  return (
    <Modal title={TranslateString(999, 'Buy now')} onDismiss={onDismiss}>
      <ModalContent>
        {error && (
          <Text color="failure" mb="8px">
            {error}
          </Text>
        )}
        <div className="modal-body">
          <p>Your are buying <span>Vlad Token</span></p>
          <div className="bidbox">
            <ul>
              <li>
                <div className="bidcontent">
                  <div className="vbid-details">
                    <p>Ask price</p>
                    <h6>0.55 BNB <span className="vladinput"><input type="text" className="form-control" placeholder="$156.98" /></span>
                      <span>
                        {/* <Link to="/" className="ctmtooltip"> */}
                        <img src="assets/images/upgraph.svg" alt="" />BONDING CURVE
                        <div className="ctmtooltiptext">
                          <h6><img src="assets/images/upgraph.svg" alt="" />BONDING CURVE</h6>
                          <hr />
                          <p>A bonding curve is a mathematical curve that defines the relationship between the price and the supply of a given asset.</p>
                          <p className="text-deem">After the sale of each NFT, the price of the next piece will automatically increase by a percentage calculated by the bonding curve.</p>
                        </div>
                        {/* </Link> */}
                      </span>
                    </h6>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <hr />
          <div className="amt-wrap">
            <div className="amt-title">
              <span>Your VLAD balance</span>
              <span>Service fee (5%)</span>
              <span>You will pay</span>
            </div>
            <div className="amt-bal">
              <span>86.001 VLAD</span>
              <span>0.501 VLAD</span>
              <span>0.501 VLAD</span>
            </div>
          </div>
          {/* <div className="btn-bidwrap">
                        <button type="button" className="btn btn-primary" data-bs-target="#buynowtoggle" data-bs-toggle="modal" data-bs-dismiss="modal">Buy Now</button>
                    </div> */}
          <div className="alertnote">
            <p><img src="assets/images/alert-triangle.svg" alt="alert icon" />Not enough funds</p>
          </div>
        </div>

        {/* <Label htmlFor="transferAddress">{TranslateString(999, 'Token ID')}:</Label>
        <Input
          id="tokenId"
          name="tokenId"
          type="number"
          placeholder={TranslateString(999, 'Enter tokenId')}
          value={values.tokenId}
          onChange={handleChange}
          isWarning={error}
          disabled={isLoading}
        /> */}
      </ModalContent>
      <Actions>
        <Button fullWidth variant="secondary" onClick={onDismiss}>
          {TranslateString(462, 'Cancel')}
        </Button>
        <Button fullWidth onClick={handleConfirm} disabled={!account || isLoading || !values.address}>
          {TranslateString(464, 'Buy now')}
        </Button>
      </Actions>
    </Modal>
  )
}

export default BuyNftModal
